@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";

.clients_div{
    background: linear-gradient(#f8984f,#cb5303);
    min-height: 100vh;
    color: white;
    padding: 5em 1em;
    gap: 4em;
    text-align: center;
}

.clients_div > h2{
    font-size: 2.5em;
    margin: 1em;
}

.clients_companies{
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
    justify-content: center;
}

